<template>
   <div class='login'>
       <div class="lxy_denglu">
           <div class="img"><img src="../assets/logo.png" alt=""></div>
         <p>
             <i class="el-icon-user-solid"></i>
            <input type="text" v-model="username">
        </p>
         <p>
             <i class="el-icon-lock"></i>
             <input type="password" v-model="password">
        </p>
         <div class="tj">
            <el-button type="primary" @click="dl">提交</el-button>
            <el-button>重置</el-button> 
         </div>   
       </div>
   </div>
</template>

<script>
export default {
   data() {
   //这里存放数据
       return {
           username:'',
           password:''
       };
   },
   //import引入的组件需要注入到对象中才能使用
   components: {

   },
   //监听属性 类似于data概念
   computed: {

   },
   //监控data中的数据变化
   watch: {

   },
   //方法集合
   methods: {
      async dl(){
           let { data:res } = await this.$http.post(`login`,{
                username:this.username,
                password:this.password
           })
           //将token存在本地,只在当前网站打开期间有效
           window.sessionStorage.setItem("Token", res.data.token);
           if(res.meta.status == 200){
             //登陆成功  
            this.$message({
                message: res.meta.msg,
                type: 'success'
                });
             this.$router.push("/sy")   
           }else{
               //登陆失败
               this.$message({
                message: res.meta.msg,
                type: 'warning'
                });
           }
           console.log(res.data)
       },

   },
   //生命周期 - 创建完成（可以访问当前this实例）
   created() {

   },
   //生命周期 - 挂载完成（可以访问DOM元素）
   mounted() {

   },
   beforeCreate() {}, //生命周期 - 创建之前
   beforeMount() {}, //生命周期 - 挂载之前
   beforeUpdate() {}, //生命周期 - 更新之前
   updated() {}, //生命周期 - 更新之后
   beforeDestroy() {}, //生命周期 - 销毁之前
   destroyed() {}, //生命周期 - 销毁完成
   activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style lang="less" scoped>
.login{
    width: 100%;
    height: 100%;
    background: #2b4b6b;
    display: flex;
    align-items: center;
    justify-content: center;
   .lxy_denglu{
        width: 400px;
        height: 300px;
        background: #fff;
       border-radius: 5px;
        position: relative;
       p{
           width: 100%;
           display: flex;
           justify-content: center;
           align-items: center;
           margin-bottom: 20px;
           position: relative;
           &:nth-of-type(1){
               margin-top: 114px;
           }
           i{
               position: absolute;
               top: 10px;
               left: 16px;
               z-index: 999;
               color: #999;
           }
           input{
               width: 94%;
               height: 38px;
               border: 1px #ccc solid;
               border-radius: 5px;
               text-indent: 20px;
               outline-color:green;
           }
          
       }
       .img{
           width: 130px;
           height: 130px;
           background: #E9EEEA;
           border-radius: 50%;
          position: absolute;
          top: -64px;
          left: 136px;
            box-shadow: 0px 0px 5px 10px rgb(252, 250, 250);
            overflow:hidden;
           img{
               width: 100%;
               overflow:hidden
           }
       }
       .tj{
           display: flex;
           padding-right: 10px;
           justify-content: flex-end;
       }

    }
}
 
</style>