<template>
   <div class='sy'>
       <el-container>
        <!-- 头部 -->
        <el-header>
            <p>
                 <img src="../assets/logo.png" alt=""> 
                 电商后台管理系统
            </p>
            <el-button type="info" @click="tuichu">退出</el-button>
        </el-header>
        <el-container>
            <!-- 左边框 -->
            <el-aside :width="iscollapse ? '64px' :'200px'">
                <div class="toggle" @click="toggle">|||</div>
                 <el-menu
                 :unique-opened="true"
                 :collapse="iscollapse"
                 :collapse-transition="false"
                 :router="true"
                 :default-active="luyou"
                 background-color="#333744"
                 text-color="#fff"
                 active-text-color="#409bff"
                class="el-menu-vertical-demo"
                @open="handleOpen"
                @close="handleClose">
                <el-submenu :index="item.id +''" v-for="(item) in lblist" :key="item.id">
                    <template slot="title">
                   <i :class="iobj[item.id]"></i>
                    <span>{{ item.authName }}</span>
                    </template>
                    <el-menu-item-group >
                    <el-menu-item :index="'/'+c.path +''" v-for="(c,index) in item.children" :key="index" @click="dianji('/'+c.path)">
                        <i class="el-icon-menu"></i>
                        {{ c.authName }}
                    </el-menu-item>
                    </el-menu-item-group>
                </el-submenu>
                </el-menu>
            </el-aside>
            <!-- 右边内容 -->
            <el-main>
              <!-- 路由的占位符 -->
              <router-view></router-view>
            </el-main>
        </el-container>
        </el-container>
   </div>
</template>

<script>
export default {
   data() {
   //这里存放数据
       return {
           lblist:[],//左侧列表
           iobj:{
               '125':"el-icon-s-custom",
               '103':"el-icon-copy-document",
               '101':"el-icon-shopping-bag-2",
               '102':"el-icon-notebook-1",
               '145':"el-icon-data-line"
           },
            iscollapse:false,
            luyou:"",//保存激活的路由
       };
   },
   //import引入的组件需要注入到对象中才能使用
   components: {

   },
   //监听属性 类似于data概念
   computed: {

   },
   //监控data中的数据变化
   watch: {

   },
   //方法集合
   methods: {
       tuichu(){
           //清除本地Token
           window.sessionStorage.clear()
           //跳回到登陆页面
           this.$router.push("/login")
           this.$message({
                message: "退出成功",
                type: 'success'
                });
       },
        handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
        //请求左边导航数据
      async getMenuList(){
        let { data:res }= await this.$http.get(`menus`)
        if(res.meta.status == 200){
             this.lblist = res.data
        }
         console.log(this.lblist)         
  
      },
      //点击缩小导航
        toggle(){
            this.iscollapse = !this.iscollapse
        },
        //点击保存被点击的路由
        dianji(v){
            console.log(v)
            window.sessionStorage.setItem("luyou",v)
            this.luyou = v
        }
   },
   //生命周期 - 创建完成（可以访问当前this实例）
   created() {
       this.getMenuList()
       this.luyou = window.sessionStorage.getItem("luyou")
   },
   //生命周期 - 挂载完成（可以访问DOM元素）
   mounted() {

   },
   beforeCreate() {}, //生命周期 - 创建之前
   beforeMount() {}, //生命周期 - 挂载之前
   beforeUpdate() {}, //生命周期 - 更新之前
   updated() {}, //生命周期 - 更新之后
   beforeDestroy() {}, //生命周期 - 销毁之前
   destroyed() {}, //生命周期 - 销毁完成
   activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style lang="less" scoped>
.sy{
    height: 100%;
}
//整体布局
.el-container{
   height: 100%;
}
//头部
.el-header{
    background: #373D41;
    display: flex;
    justify-content: space-between;
    align-items: center;
    p{
        height: 100%;
        font-size: 24px;
        color: #fff;
        display: flex;
        align-items: center;
        img{
           height: 100%;
        }
        
    }
}
//左边
.el-aside{
    background: #333744;
    .el-menu{
        border-right:none;
    }
}
//右边
.el-main{
    background: #eaedf1;
}
//点击左边导航
.toggle{
    background: #4a5064;
    color: #fff;
    text-align: center;
    line-height: 24px;
    font-size: 10px;
    cursor: pointer;
    letter-spacing: 0.2em;
}
</style>