import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from'../components/Login.vue'
import Sy from'../components/Sy.vue'
import Wekcome from '../components/Wekcome.vue'
import Users from '../components/page/Users.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    redirect:"/login"
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  //登录页面
  {
    path: '/login',
    name: 'Login',
    component: () => import('../components/Login.vue')
  },
  //登录成功后的页面
  {
    path: '/sy',
    name: 'Sy',
    component: () => import('../components/Sy.vue'),
    redirect:"/wekcome",
    children:[
      {
        path:"/wekcome",
        component: () => import('../components/Wekcome.vue'),
      },
      {
        path:"/users",
        component: () => import('../components/page/Users.vue'),
      }
    ]
  }
]

const router = new VueRouter({
  routes
})

export default router
