<template>
   <div class='users'>
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/sy' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>用户管理</el-breadcrumb-item>
            <el-breadcrumb-item>用户列表</el-breadcrumb-item>
        </el-breadcrumb>
        <el-card>
            <!-- 搜索和添加 -->
            <el-row :gutter="20">
                <el-col :span="10">
                    <el-input  clearable @clear='getuserList' v-model="queryInfo.query" placeholder="请输入内容" class="input-with-select">
                        <el-button @click="getuserList" slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="6">
                     <el-button type="primary"  @click="dialogVisible =true">添加用户</el-button>
                </el-col>
            </el-row>
            <!-- 列表 -->
             <el-table
                :data="list"
                border stripe
                style="width: 100%">
                <el-table-column
                   type ="index">
                </el-table-column>
                <el-table-column
                    prop="username"
                    label="姓名"
                    width="180">
                </el-table-column>
                 <el-table-column
                    prop="email"
                    label="邮箱"
                    width="180">
                </el-table-column>
                 <el-table-column
                    prop="mobile"
                    label="电话"
                    width="180">
                </el-table-column>
                <el-table-column
                    prop="role_name"
                    label="角色">
                </el-table-column>
                <el-table-column label="状态"  width="80">
                    <template slot-scope="scope">
                        <el-switch @change="xgzt(scope.row)" v-model="scope.row.mg_state"></el-switch>
                    </template>
                </el-table-column>
                <el-table-column label="操作"  width="220">
                    <template slot-scope="scope">
                            <!-- 修改按钮 -->
                             <el-tooltip  effect="dark" content="修改按钮" placement="top" :enterable="false">
                            <el-button @click="xiugai(scope.row)" type="primary" size="mini" icon="el-icon-edit" ></el-button>
                            </el-tooltip>
                            <!-- 删除按钮 -->
                             <el-tooltip  effect="dark" content="删除按钮" placement="top" :enterable="false">
                            <el-button @click="shanchu(scope.row)" type="danger" size="mini" icon="el-icon-delete" ></el-button>
                            </el-tooltip>
                            <!-- 分配角色按钮 -->
                            <el-tooltip  effect="dark" content="分配角色按钮" placement="top" :enterable="false">
                                <el-button type="warning" size="mini" icon="el-icon-setting"></el-button>
                            </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>
             <!-- 分页 -->
            <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryInfo.pagenum"
            :page-sizes="[1, 2, 5, 10]"
            :page-size="queryInfo.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
            </el-pagination>
        </el-card>
        <!-- 添加的弹框 -->
        <el-dialog
            title="添加用户"
            :visible.sync="dialogVisible"
            @close="adddialog"
            width="60%">
            <!-- 内容主体区 -->
            <el-form :model="addFrom" :rules="addRules"  ref="addref" label-width="70px">
                <el-form-item  label="用户名" prop="username">
                    <el-input v-model="addFrom.username"></el-input>
                </el-form-item>
                <el-form-item  label="密码" prop="password">
                    <el-input type="password" v-model="addFrom.password"></el-input>
                </el-form-item>
                <el-form-item  label="邮箱" prop="email">
                    <el-input v-model="addFrom.email"></el-input>
                </el-form-item>
                <el-form-item  label="手机" prop="mobile">
                    <el-input v-model="addFrom.mobile"></el-input>
                </el-form-item>
            </el-form>
            <!-- 底部按钮区 -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible=false">取 消</el-button>
                <el-button type="primary" @click="addqd">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 修改的弹框 -->
        <el-dialog
            title="修改用户"
            :visible.sync="dialogVisible1"
            @close="xiugaidialog"
            width="60%">
            <!-- 内容主体区 -->
            <el-form :model="xiugaiFrom" :rules="addRules"  ref="xiugairef" label-width="70px">
                <el-form-item  label="用户名" prop="username">
                    <el-input v-model="xiugaiFrom.username"  :disabled="true"></el-input>
                </el-form-item>
                <el-form-item  label="邮箱" prop="email">
                    <el-input v-model="xiugaiFrom.email"></el-input>
                </el-form-item>
                <el-form-item  label="手机" prop="mobile">
                    <el-input v-model="xiugaiFrom.mobile"></el-input>
                </el-form-item>
            </el-form>
            <!-- 底部按钮区 -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible1=false">取 消</el-button>
                <el-button type="primary" @click="xiugaiqd">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 删除的弹框 -->

   </div>
</template>

<script>
export default {
   data() {
   //这里存放数据
   //邮箱验证
 var  checkEmail = (rule,value,cb)=>{
        let yx = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
        if(yx.test(value)){

            return cb()
        }
        cb(new Error('邮箱格式不对'))
    }
    //手机验证
    var checkMobile =(rule,value,cb)=>{
         let sj =/^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
        if(sj.test(value)){

            return cb()
        }
        cb(new Error('手机号格式不对'))
    }       
       return {
           queryInfo:{
               query:"",
               //当前的页数
               pagenum:1,
               //当前每页显示多少条数据
               pagesize:2
           },
           list:[],
            total:0,
            //添加弹框的显示
             dialogVisible:false,
             //修改的弹框
             dialogVisible1:false,
             //添加表单数据
             addFrom:{
                 username:"",
                 password:"",
                 mobile:"",
                 email:""   
             },
             //修改用户信息
              xiugaiFrom:{
                 username:"",
                 mobile:"",
                 email:"" ,
                 id:""  
             },
             //表单验证
             addRules:{
                 //用户名
                  username: [
                        { required: true, message: '请输入用户名', trigger: 'blur' },
                        { min: 1, max: 5, message: '长度在 1 到 5 个字符', trigger: 'blur' }
                    ],
                    //密码
                    password:[
                        { required: true, message: '请输入密码', trigger: 'blur' },
                        { min: 6, max: 14, message: '长度在 6 到 14 个字符', trigger: 'blur' }
                    ],
                    //手机
                     mobile:[
                         { required: true, trigger: 'blur' },
                        {  validator: checkMobile, trigger: 'blur' }
                     ],
                    //邮箱
                    email:[
                          { required: true,  trigger: 'blur' },
                             { validator: checkEmail, trigger: 'blur' }
                    ]
             },
               
       };
   },
   //import引入的组件需要注入到对象中才能使用
   components: {

   },
   //监听属性 类似于data概念
   computed: {

   },
   //监控data中的数据变化
   watch: {

   },
   //方法集合
   methods: {
       //首次获取列表数据
       //然后搜索
      async getuserList(){
          let { data:res } = await this.$http.get(`users`,{params:this.queryInfo})
          console.log(res)
          if(res.meta.status == 200){
              this.list = res.data.users
              this.total = res.data.total
              console.log(this.list)
          }else{
              this.$message.error("获取用户列表数据失败")
          }
       },
       //分页
       //监听pagesize改变的事件
       handleSizeChange(newsize){
        //    console.log(newsize)
           //切换每页显示条数
           this.queryInfo.pagesize = newsize
           this.getuserList()
       },
       //监听页码值 改变的事件
       handleCurrentChange(newpage){
        //    console.log(newpage)
        //    切换页数
           this.queryInfo.pagenum = newpage
           this.getuserList()
       },
       //状态的改变
      async xgzt(v){
        //    console.log(v)
          let {data:res} = await this.$http.put(`users/${v.id}/state/${v.mg_state}`)
            // console.log(res)
            if(res.meta.status == 200){
               this.$message({
                message: res.meta.msg,
                type: 'success'
                });
            }else{
                this.$message({
                message:"状态修改失败",
                type: 'warning'
                });
                v.mg_state = !v.mg_state
            }
       },
       //添加表单重置
       adddialog(){
           this.$refs.addref.resetFields()
       },
       //修改表单的重置
       xiugaidialog(){
           this.$refs.xiugairef.resetFields()
       },
    //    添加表单验证
        addqd(){
            this.$refs.addref.validate(async valid =>{
                // console.log(valid)
                if (!valid) return
                //可以发起添加用户的请求
              let { data:res } = await this.$http.post(`users`,this.addFrom)
                    // console.log(res) 
                    if(res.meta.status == 201){
                        //添加成功  
                        this.$message({
                            message: res.meta.msg,
                            type: 'success'
                            });   
                    }else{
                        //添加失败
                        this.$message({
                            message: res.meta.msg,
                            type: 'warning'
                            });
                    }
                    this.dialogVisible = false
            })
            
        },
        //修改按钮
      async xiugai(v){
           console.log(v)
        let {data:res} = await this.$http.get(`users/${ v.id }`)
            // console.log(res)
            if(res.meta.status !== 200){
             return this.$message({
                    message: res.meta.msg,
                    type: 'warning'
                    });
            }
            this.xiugaiFrom.username = res.data.username
            this.xiugaiFrom.mobile = res.data.mobile
            this.xiugaiFrom.email =res.data.email
             this.xiugaiFrom.id =res.data.id
            this.dialogVisible1 = true
        },
        //修改确定
        xiugaiqd(){
            this.$refs.xiugairef.validate(async valid =>{
                // console.log(valid)
                if (!valid) return
                //可以发起添加用户的请求
              let { data:res } = await this.$http.put(`users/${this.xiugaiFrom.id}`,this.xiugaiFrom)
                    // console.log(res) 
                    if(res.meta.status == 200){
                        //修改成功  
                        this.$message({
                            message: res.meta.msg,
                            type: 'success'
                            });   
                    }else{
                        //修改失败
                        this.$message({
                            message: res.meta.msg,
                            type: 'warning'
                            });
                    }
                    this.dialogVisible1 = false
                    //再次调用列表数据
                    this.getuserList()
            })
        },
        //删除按钮
       async shanchu(v) {
            console.log(v.id)
        let conshanchu= await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err =>{
            return err
        })
        //如果不是字符串confirm，那么就是取消删除
        if(conshanchu !== "confirm"){
            return this.$message.info("已经取消删除")
        }
        //如果点击确定，就删除
        let { data:res } = await this.$http.delete('users/'+v.id)
        console.log(res)
        if(res.meta.status == 200){
            //删除成功  
            this.$message({
                message: res.meta.msg,
                type: 'success'
                });   
        }else{
            //删除失败
            this.$message({
                message: res.meta.msg,
                type: 'warning'
                });
        }
        //再次调用列表数据
        this.getuserList()
      }

   },
   //生命周期 - 创建完成（可以访问当前this实例）
   created() {
       this.getuserList()
   },
   //生命周期 - 挂载完成（可以访问DOM元素）
   mounted() {

   },
   beforeCreate() {}, //生命周期 - 创建之前
   beforeMount() {}, //生命周期 - 挂载之前
   beforeUpdate() {}, //生命周期 - 更新之前
   updated() {}, //生命周期 - 更新之后
   beforeDestroy() {}, //生命周期 - 销毁之前
   destroyed() {}, //生命周期 - 销毁完成
   activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style lang="less" scoped>
.el-card{
    width: 100%;
    margin-top: 20px;
}
.el-table{
    margin-top: 20px;
}
.el-pagination{
    margin-top: 20px;
}
</style>